
const VideoAttachment = {
  props: ['attachment', 'controls'],
  computed: {
    isGifv () {
      return this.attachment.mimetype === 'image/gifv'
    },
    playOnHover () {
      return this.$store.state.config.stopGifs
    },
    autoplay () {
      if (this.playOnHover && this.isGifv) {
        return !this.controls
      }
      return this.isGifv && this.controls
    },
    showControls () {
      return !this.isGifv && this.controls
    },
    loopVideo () {
      return this.$store.state.config.loopVideo || this.isGifv
    }
  },
  methods: {
    onMouseEnter (e) {
      if (this.playOnHover && this.isGifv) {
        const video = this.$el.querySelector('video')
        video.play()
      }
    },
    onMouseLeave (e) {
      if (this.playOnHover && this.isGifv) {
        const video = this.$el.querySelector('video')
        video.pause()
      }
    },
    onVideoDataLoad (e) {
      const target = e.srcElement || e.target
      if (typeof target.webkitAudioDecodedByteCount !== 'undefined') {
        // non-zero if video has audio track
        if (target.webkitAudioDecodedByteCount > 0) {
          this.loopVideo = this.loopVideo && !this.$store.state.config.loopVideoSilentOnly
        }
      } else if (typeof target.mozHasAudio !== 'undefined') {
        // true if video has audio track
        if (target.mozHasAudio) {
          this.loopVideo = this.loopVideo && !this.$store.state.config.loopVideoSilentOnly
        }
      } else if (typeof target.audioTracks !== 'undefined') {
        if (target.audioTracks.length > 0) {
          this.loopVideo = this.loopVideo && !this.$store.state.config.loopVideoSilentOnly
        }
      }
    }
  }
}

export default VideoAttachment



